<template>
  <div
    @click="botones = 1"
    v-if="botones === 0"
    class="actividad relative z-10"
  >
    <div
      class="
        w-6
        h-6
        rounded-full
        flex
        items-center
        justify-center
        border-2
        flex-none
        mr-2
        relative
        z-10
      "
      :class="{
        'bg-green-200 border-green-500 text-green-500': array.valor === 1,
        'bg-red-200 border-red-500 text-red-500': array.valor === 2,
        'bg-naranja1a-200 border-naranja1a-500 text-naranja1a-500':
          array.valor === 3,
      }"
    >
      <h1 v-if="array.valor === 1">SI</h1>
      <h1 v-if="array.valor === 2">NO</h1>
      <h1 class="text-xxs" v-if="array.valor === 3">N/A</h1>

      <!-- ICONO FOTO -->
      <div
        v-if="array.tieneFoto === 'SI'"
        class="
          absolute
          -left-full
          bottom-3
          bg-blue-200
          text-blue-500
          flex
          items-center
          justify-center
          w-4
          h-4
          rounded-full
          shadow
        "
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="w-3 h-3"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <g transform="translate(2 3)">
            <path
              d="M20 16a2 2 0 0 1-2 2H2a2 2 0 0 1-2-2V5c0-1.1.9-2 2-2h3l2-3h6l2 3h3a2 2 0 0 1 2 2v11z"
            />
            <circle cx="10" cy="10" r="4" />
          </g>
        </svg>
      </div>

      <!-- ICONO COMENTARIO -->
      <div
        v-if="array.tieneComentario === 'SI'"
        class="
          absolute
          -left-full
          top-3
          bg-indigo-200
          text-indigo-500
          flex
          items-center
          justify-center
          w-4
          h-4
          rounded-full
          shadow
        "
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="w-3 h-3"
          viewBox="0 0 24 24"
          fill="none"
          stroke="currentColor"
          stroke-width="3"
          stroke-linecap="round"
          stroke-linejoin="round"
        >
          <path
            d="M21 15a2 2 0 0 1-2 2H7l-4 4V5a2 2 0 0 1 2-2h14a2 2 0 0 1 2 2z"
          ></path>
        </svg>
      </div>

      <!-- ICONO REPORTADO -->
      <div
        v-if="array.reportado === 'SI'"
        @click="reportado(array)"
        class="
          absolute
          -left-full
          top-3
          bg-green-300
          text-green-600
          flex
          items-center
          justify-center
          w-4
          h-4
          rounded-full
          shadow
          cursor-pointer
        "
      >
        R
      </div>
    </div>
    <h1 class="font-bold mr-1">{{ index }}.</h1>
    <h1 class="">{{ array.actividad }}</h1>

    <!-- BOTÓN REPORTADO -->
    <div
      v-if="array.reportado == 'NO' && array.valor == 2"
      @click="reportado(array)"
      class="
        w-auto
        bg-purple-300
        text-purple-600
        absolute
        right-1
        rounded-full
        cursor-pointer
        z-10
        flex
        px-2
        py-0.5
        justify-items-center
        text-sm
      "
    >
      ¿Reportar?
    </div>
  </div>

  <div v-if="botones === 1" @click="botones = 0" class="actividad">
    <div
      v-if="array.tieneFoto === 'SI'"
      class="w-36 h-36 rounded flex-none bg-cover bg-center"
      :style="{ backgroundImage: 'url(' + array.foto + ')' }"
    ></div>

    <div v-if="array.tieneComentario === 'SI'" class="text-justify p-2">
      <h1>{{ array.comentario }}</h1>
    </div>

    <div v-if="array.tieneComentario === 'NO'" class="text-justify p-2">
      <h1>Sin Comentario</h1>
    </div>
  </div>
</template>

<script>
import { useStore } from "vuex";
import { ref } from "@vue/reactivity";

export default {
  props: {
    array: { type: Object },
    index: { type: Number },
  },

  setup(props) {
    const store = useStore();
    const Swal = require("sweetalert2");

    const botones = ref(0);

    const reportado = async (item) => {
      const response = await store.dispatch("accionesUsuario", {
        idDestino: localStorage.getItem("idDestino"),
        idUsuario: localStorage.getItem("usuario"),
        action: "reportado",
        idCaptura: item.idCaptura,
        reportado: item.reportado,
      });

      botones.value = 0;

      if (response.response) {
        props.array.reportado = response.data.data;
      }

      if (!response.response) {
        Swal.fire({
          title: "Reporte NO Actualizado!",
          icon: "info",
          background: "#2c3e50",
          customClass: {
            popup: "colored-toast",
            backdrop: "swal2-backdrop-show",
            icon: "swal2-icon-show",
          },
          iconColor: "#fff",
        });
      }
    };

    return { botones, reportado };
  },
};
</script>

<style scoped>
.actividad {
  @apply flex flex-row items-center justify-start text-left w-full py-2 bg-white px-2 text-xs font-semibold border-b;
}

.actividad:hover {
  @apply bg-blue-100;
}

.actividad:last-child {
  @apply border-b-0 rounded-b-xl;
}

.actividad:first-child {
  @apply rounded-t-xl;
}

button {
  @apply p-2 rounded mr-2 font-bold w-1/5 flex items-center justify-center focus:outline-none;
}

button:last-child {
  @apply mr-0;
}
</style>
