<template>
  <div
    class="
      w-6
      h-6
      hijo
      flex-none flex
      items-center
      justify-center
      text-xs
      font-bold
      relative
      text-black
    "
    :class="{
      cero: color === 0,
      uno: color === 1,
      dos: color === 2,
      tres: color === 3,
      planificado: color === 0 && planificado === 'PLANIFICADO',
      ceroBorder: color === 0 && planificado !== 'PLANIFICADO',
      unoBorder: color === 1 && planificado !== 'PLANIFICADO',
      dosBorder: color === 2 && planificado !== 'PLANIFICADO',
      tresBorder: color === 3 && planificado !== 'PLANIFICADO',
      planificadoBorder: planificado === 'PLANIFICADO',
    }"
    :style="{ 'grid-column-start': semana, 'grid-column-end': semana }"
  >
    <p
      class="
        cursor-pointer
        w-full
        h-full
        flex
        justify-center
        items-center
        rounded-sm
      "
      @click="mostrar = !mostrar"
    >
      {{ valor }}
    </p>

    <!-- ICONO REPORTADO -->
    <div
      v-if="reportado"
      class="
        absolute
        left-2
        top-4
        bg-green-300
        text-green-600
        flex
        items-center
        justify-center
        w-4
        h-4
        rounded-full
        shadow
        cursor-pointer
      "
    >
      R
    </div>

    <div
      v-if="mostrar && encabezado !== 'si'"
      class="
        absolute
        -top-full
        px-2
        py-1
        bg-gray-300
        z-50
        text-xs
        rounded-xl
        flex flex-col
        items-center
        justify-start
        w-72
      "
    >
      <h2 class="w-full text-black font-bold text-sm flex flex-col truncate">
        {{ array.equipo }}
        <span class="text-xs text-blue-500">Semana: {{ semana }}</span>
      </h2>

      <button
        class="
          flex
          justify-center
          items-center
          text-red-400
          font-semibold
          absolute
          -top-0
          -right-0
        "
        @click="mostrar = !mostrar"
      >
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="h-6 w-6"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            stroke-width="2"
            d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
          />
        </svg>
      </button>

      <div class="w-full mt-2 flex flex-row justify-center item-center">
        <button
          class="
            bg-blue-200
            text-blue-500
            mb-2
            mx-1
            w-1/2
            py-1.5
            md:py-2
            rounded-lg
            font-bold
          "
          :class="{ 'ring-2 ring-gray-50': !vista }"
          @click="vista = false"
        >
          Planificar
        </button>

        <button
          class="
            bg-green-200
            text-green-400
            mb-2
            mx-1
            w-1/2
            py-1.5
            md:py-2
            rounded-lg
            font-bold
          "
          :class="{ 'ring-2 ring-gray-50': vista }"
          @click="vista = true"
        >
          Ver Detalles
        </button>
      </div>

      <div class="w-full text-black" v-if="vista">
        <p
          v-if="!arrayActividades.length"
          class="w-full bg-white p-2 mb-2 rounded-lg cursor-pointer text-center"
        >
          Sin Registros
        </p>

        <div
          @click="
            mostrarOT = !mostrarOT;
            detallesActividad(item.idRegistro);
            mostrar = !mostrar;
          "
          v-for="(item, index) in arrayActividades"
          :key="index"
          class="actividad relative cursor-pointer"
        >
          <h1 class="">{{ item.fecha }}</h1>
          <h1 class="mx-1.5">-</h1>
          <h1>{{ item.creadoPor }}</h1>

          <!-- ICONO REPORTADO -->
          <div
            v-if="item.reportado === 1"
            class="
              absolute
              -left-3
              top-1
              bg-green-300
              text-green-600
              flex
              items-center
              justify-center
              w-4
              h-4
              rounded-full
              shadow
              cursor-pointer
            "
          >
            R
          </div>
        </div>
      </div>

      <div class="w-full text-black bg-white rounded-lg p-2" v-if="!vista">
        <p class="pb-0.5">PLANIFICACIÓN</p>

        <!-- PLANIFICAR DESDE AQUÍ -->
        <div
          class="
            w-full
            h-10
            mt-1
            flex flex-row
            justify-start
            item-center
            bg-blue-100
            py-1.5
            md:py-2
            px-2
            rounded-lg
          "
        >
          <span class="w-2/6 py-1 text-blue-500">Desde Aquí</span>
          <input
            type="number"
            placeholder="Frecuencia"
            class="w-2/6 ring ml-1 px-1 rounded-l-lg focus:ring"
            min="1"
            max="52"
            v-model="frecuencia"
          />
          <button
            class="
              w-2/6
              ring ring-blue-300
              bg-blue-300
              rounded-r-lg
              px-2
              hover:ring-blue-400 hover:bg-blue-400
              font-bold
            "
            @click="planificacion(semana, 'planificarDesdeAqui')"
          >
            <span class="text-blue-500">Aplicar</span>
          </button>
        </div>

        <!-- PLANIFICAR INDIVIDUAL -->
        <div
          class="
            w-full
            h-10
            mt-1
            flex flex-row
            justify-start
            item-center
            bg-blue-100
            py-1.5
            md:py-2
            px-2
            rounded-lg
          "
        >
          <span class="w-1/3 py-1 text-blue-500">Individual</span>

          <button
            class="
              w-2/6
              ring ring-blue-300
              bg-blue-300
              rounded-lg
              px-2
              hover:ring-blue-400 hover:bg-blue-400
              font-bold
            "
            @click="planificacion(semana, 'planificarIndividual')"
          >
            <span class="text-blue-500">Aplicar</span>
          </button>
        </div>

        <p class="pt-3 pb-2">ELIMINAR PLANIFICACIÓN</p>

        <div class="flex flex-row justify-center items-center">
          <!-- ELIMINAR DESDE AQUÍ -->
          <button
            class="
              w-1/2
              h-6
              md:h-8
              mx-5
              ring ring-red-300
              bg-red-300
              rounded-lg
              hover:ring-red-400 hover:bg-red-400
              font-bold
            "
            @click="planificacion(semana, 'eliminarDesdeAqui')"
          >
            Desde Aquí
          </button>

          <!-- ELIMINAR DESDE AQUÍ -->
          <button
            class="
              w-1/2
              h-6
              md:h-8
              mx-5
              ring ring-red-300
              bg-red-300
              rounded-lg
              hover:ring-red-400 hover:bg-red-400
              font-bold
            "
            @click="planificacion(semana, 'eliminarIndividual')"
          >
            Individual
          </button>
        </div>
      </div>

      <div class="pt-2 text-black">{{ msj }}</div>
    </div>

    <!-- SHOW OT -->
    <teleport to="#modales" v-if="mostrarOT">
      <div
        class="
          absolute
          top-0
          pt-24
          flex flex-col
          items-center
          justify-start
          z-50
          overflow-y-auto
          w-full
          h-screen
          bg-black
        "
        v-for="(item, index) in detalles"
        :key="index"
      >
        <!-- CERRAR OT -->
        <div
          class="
            absolute
            top-0
            py-2
            px-4
            rounded-b-3xl
            bg-gray-300
            opacity-60
            cursor-pointer
          "
          @click="mostrarOT = !mostrarOT"
        >
          <h1>Clic para cerrar</h1>
        </div>

        <div class="w-full md:w-1/4 flex flex-col items-center justify-start">
          <div
            class="
              px-4
              pb-4
              w-full
              flex flex-col
              items-start
              justify-start
              text-left
            "
          >
            <div class="mb-4 text-white">
              <h1 class="font-bold uppercase">
                {{ item.equipo }}
              </h1>
              <h1 class="font-bold uppercase">
                {{ item.sabana }}
              </h1>
              <h1 class="font-bold uppercase">
                Por: <span class="italic">{{ item.creadoPor }}</span>
              </h1>
              <h1 class="font-bold uppercase">
                El: <span class="italic">{{ item.fechaCreado }}</span>
              </h1>
            </div>

            <!-- Listado -->
            <div
              class="
                flex flex-col
                items-center
                justify-start
                w-full
                shadow
                rounded-xl
              "
            >
              <div
                class="
                  flex flex-col
                  items-center
                  justify-start
                  w-full
                  shadow
                  rounded-xl
                "
              >
                <SemanaApartadoSabanas
                  v-for="(item, index) in item.apartados"
                  :key="index"
                  :apartado="item.apartado"
                  :array="item"
                />
              </div>
            </div>
            <!-- Listado -->
          </div>
        </div>
      </div>
    </teleport>
    <!-- SHOW OT -->
  </div>
</template>

<script>
import { ref } from "@vue/reactivity";
import SemanaApartadoSabanas from "./SemanaApartadoSabanas.vue";
import { useStore } from "vuex";
import { CheckIcon } from "@heroicons/vue/solid";
import { useRoute } from "vue-router";
import uniqid from "uniqid";

export default {
  name: "SemanasCuadrito",

  components: { SemanaApartadoSabanas, CheckIcon },

  props: {
    array: { type: Object, value: [] },
    semana: { type: Number, required: true },
    cantidad: { type: Number, required: false },
    encabezado: { type: String, required: false },
    planificado: { type: String },
  },

  setup(props) {
    const dispatch = useStore().dispatch;
    const state = useStore().state;
    const route = useRoute();
    const valor = ref();
    const mostrar = ref(false);
    const mostrarOT = ref(false);
    const color = ref(0);
    const detalles = ref([]);
    const reportado = ref(false);
    const arrayActividades = ref([]);
    const vista = ref(false);
    const frecuencia = ref("");
    const idEquipo = props.array.idEquipo;
    const msj = ref("");

    if (props.encabezado == "si") {
      props.cantidad > 0 ? (valor.value = props.cantidad) : "";
      color.value = 3;
    }

    const detallesActividad = async (idRegistro) => {
      const response = await dispatch("accionesUsuario", {
        idDestino: route.params.idDestino,
        idUsuario: localStorage.getItem("usuario"),
        action: "detallesActividad",
        idRegistro: idRegistro,
      });

      if (response.response) detalles.value = response.data.data;

      if (!response.response) detalles.value = [];
    };

    if (props.array.registros) {
      let cantidad = 0;
      props.array.registros.forEach((element) => {
        if (element.semana == props.semana) {
          arrayActividades.value.push(element);

          if (props.encabezado == "no") {
            cantidad++;
            color.value = element.color;

            if (element.reportado === 1) reportado.value = true;
          }
        }
      });

      if (cantidad > 0) valor.value = cantidad;
      else valor.value = "";
    }

    const planificacion = async (semana, opcion) => {
      if (semana < 1 || semana > 52) {
        return;
      }

      if (
        (opcion === "planificarDesdeAqui" &&
          Math.trunc(frecuencia.value) < 1) ||
        Math.trunc(frecuencia.value) > 52
      ) {
        frecuencia.value = "";
        return;
      }

      msj.value = "Procesando...";

      let idPlaneacion = "";

      if (props.array.planificacion.length)
        idPlaneacion = props.array.planificacion[0].id_publico;

      if (!props.array.planificacion.length) idPlaneacion = uniqid() + uniqid();

      const data = {
        idDestino: route.params.idDestino,
        idUsuario: localStorage.getItem("usuario"),
        action: "planificacion",
        idEquipo: idEquipo,
        semana: semana,
        opcion: opcion,
        frecuencia: frecuencia.value,
        planificacion: props.array.planificacion.length ? true : false,
        idPlaneacion: idPlaneacion,
      };

      const response = await dispatch("accionesUsuario", data);
      if (response.response) {
        const data = response.data.data;
        const array = state.array[0].equipos;
        msj.value = "¡Proceso Finalizado!";

        array.forEach((item) => {
          if (item.idEquipo == idEquipo) {
            item.planificacion = data;
          }
        });

        msj.value = "";
        mostrar.value = false;
        return;
      }

      if (!response.response) {
        msj.value = "¡Datos Incorrectos!";

        setTimeout(() => {
          msj.value = "";
        }, 1200);
        return;
      }
    };

    if (arrayActividades.value.length) vista.value = true;

    return {
      valor,
      mostrar,
      mostrarOT,
      arrayActividades,
      color,
      detallesActividad,
      frecuencia,
      detalles,
      reportado,
      vista,
      planificacion,
      msj,
    };
  },
};
</script>

<style scoped>
.hijo {
  grid-row-start: 1;
  border-width: 0.16rem;
  border-radius: 4px;
  margin-right: 2px;
}

.cero {
  color: #414141;
  background-color: rgb(255, 255, 255);
}

.uno {
  color: #80c722;
  background-color: rgb(230, 255, 215);
}

.dos {
  color: #ff9d42;
  background-color: rgb(255, 234, 215);
}

.tres {
  border-width: 0;
}

.tres:hover {
  background-color: whitesmoke;
}
.ceroBorder {
  border-color: #414141;
}

.unoBorder {
  border-color: #80c722;
}

.dosBorder {
  border-color: #ff9d42;
}

.tres:hover {
  background-color: whitesmoke;
}
.planificado {
  color: #88bdff !important;
  background: #a5cbf8 !important;
  border-color: #53a0ff !important;
}
.planificadoBorder {
  border-color: #53a0ff !important;
}

.actividad {
  @apply flex flex-row items-center justify-start text-left w-full py-2 bg-white px-2 text-xs font-semibold border-b;
}

.actividad:hover {
  @apply bg-blue-100;
}

.actividad:last-child {
  @apply border-b-0 rounded-b-xl;
}

.actividad:first-child {
  @apply rounded-t-xl;
}
</style>